export default {
	data() {
		return {
			loading: false,
			finish: false
		};
	},

	methods: {
		go(action) {
			if (action == "start") {
				this.loading = false;
				this.finish = false;
			}

			if (action == "load") {
				this.loading = true;
			}

			if (action == "finish") {
				this.finish = true;
			}
		}
	}
};
